.offers{
    width: 65%;
    display: flex;
    margin: auto;
    margin-top: 190px;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #fb8dda,#f5b2fb6c 60%);
}

.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.offers-left h1{
    color:rgb(0, 0, 185) ;
    font-size: 80px;
    font-weight: 600;
}

.offers-left p{
    color: blue;
    font-size: 22px;
    font-weight: 600;
}

.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background: #da2ea6;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}

.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;

}

