:root{
    --color:rgb(8, 8, 158);
}

.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;

}

.popular h1{
    color: var(--color);
    font-size: 50px;
    font-weight: 600;
}

.popular hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background-color: chocolate;
}

.popular-item{
    margin-top: 50px;
    display: flex;
    gap: 30px;
}



